import axios from 'axios'
import iMask from 'imask'

export default () => {
  // focused
  const $wraps = document.querySelectorAll('.input-float')

  $wraps.forEach(($wrap) => {
    // input
    const $input = $wrap.querySelector('input')

    if ($input) {
      $input.addEventListener('keyup', (e) => {
        const { value } = e.target
  
        if (value.trim() !== '') {
          $input.classList.add('focused')
        } else {
          $input.classList.remove('focused')
        }
      })
    }

    // select
    const $select = $wrap.querySelector('select')

    if ($select) {
      $select.addEventListener('change', (e) => {
        const { value } = e.target
  
        if (value.trim() !== '') {
          $select.classList.add('focused')
        } else {
          $select.classList.remove('focused')
        }
      })
    }
  })  

  // mask
  const $inputsPhone = document.querySelectorAll('[type="tel"]')

  $inputsPhone.forEach(($input) => 
    iMask($input, { mask: '(00) 00000-0000' }))
  

  // success
  const handleSuccess = ($form) => {
    $form.innerHTML = `
      <div class="icon-success">
        <svg xmlns="http://www.w3.org/2000/svg" width="72px" height="72px">
          <g fill="none" stroke="#8EC343" stroke-width="2">
            <circle cx="36" cy="36" r="35" style="stroke-dasharray:240px, 240px; stroke-dashoffset: 480px;"></circle>
            <path d="M17.417,37.778l9.93,9.909l25.444-25.393" style="stroke-dasharray:50px, 50px; stroke-dashoffset: 0px;"></path>
          </g>
        </svg>

        <p>Obrigado! Em breve, um de nossos Facilitadores entrará em contato para solicitar 
        a documentação necessária para a análise do seu crédito imobiliário.</p>
      </div>
    `
  }

  // save on database
  const saveOnDatabase = (data) => {
    axios.post('http://142.93.49.85:3333/api/users', data)
    .then((success) => console.log('success'))
    .catch(console.error)
  }


  // submit
  const $forms = document.querySelectorAll('form')

  $forms.forEach(($form) => {
    $form.addEventListener('submit', (e) => {
      e.preventDefault()

      $form.classList.add('loading')

      const [name, email, number, locale] = e.target
      const [ddd, phone] = number.value.split(' ')

      const enterprises = {
        3195: 34003,
        3173: 34002
      }

      const data = {
        property: enterprises[locale.value],
        locale: locale.value,
        name: name.value,
        email: email.value,
        ddd: ddd.replace(/[^0-9.]/g, ''),
        number: phone.replace(/[^0-9.]/g, '')
      }

      const resHC = hc_envia_mensagem(
        data.property,
        data.name,
        data.email,
        data.ddd,
        data.number,
        'Origin: Landing Page - Aprova Fácil',
        data.locale
      )

      const response = JSON.parse(resHC)

      if (response.sucesso) {
        saveOnDatabase({
          name: name.value,
          email: email.value,
          whatsapp: number.value,
          locale: locale.value
        })

        setTimeout(() => {
          $form.classList.remove('loading')
          handleSuccess($form)
        }, 1000);
      } else {
        $form.classList.remove('loading')

        console.error(response.retorno)
        alert(response.retorno)
      }
    })
  })
}
